<template>
  <div class="setting">
    <h1>基本信息</h1>
    <div style="padding: 0 20px;">
      <a-form
        layout="inline"
        :label-col="{md: 2, xs: 3}"
        labelAlign="right"
      >
        <a-form-item label="账号">
          {{ params.account }}
        </a-form-item>
        <a-form-item label="姓名">
          <a-input v-model="params.name" />
        </a-form-item>
        <a-form-item label="头像">
          <a-upload
            @change="handleChange"
            :show-upload-list="false"
            :action="`${proxy}/city_book/upload/upload`"
          >
            <div
              v-if="params.avatar"
              class="bg_img"
              :style="`background-image: url(${proxy}/cbh_file/${params.avatar}); width: 148px; height: 148px;`"
            ></div>
            <div v-else>
              <a-icon :type="iconLoading ? 'loading' : 'plus'" />
              <div class="ant-upload-text">
                Upload
              </div>
            </div>
          </a-upload>
        </a-form-item>
        <a-form-item label="性别">

          <a-radio-group v-model="params.gender" @change="sexChange">
            <a-radio :value="1">
              男
            </a-radio>
            <a-radio :value="2">
              女
            </a-radio>
          </a-radio-group>

        </a-form-item>
        <a-form-item label="手机号">
          <a-input v-model="params.phone" />
        </a-form-item>
      </a-form>

      <div>
        <a-button type="primary" @click="submit" :loading="submitLoading">保存</a-button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from 'vuex'
export default {
  name: 'Setting',
  data() {
    let params = {
      account: '',
      name: '',
      gender: 1,
      phone: '',
      avatar: ''
    }
    return {
      params,
      avatarImg: '',
      iconLoading: false,
      sex: 1,
      isEditPhone: false,
      phone: '',
      proxy: process.env.VUE_APP_API_URL,
      submitLoading: false
    }
  },
  computed: {
    ...mapState('login', ['user']),
  },
  watch: {
    'user'() {
      this.echoData()
    }
  },
  methods: {
    ...mapActions('manager', {
      updateManager: 'updateManager'
    }),
    handleChange(file) {
      console.log(file.file)
      if (file.file.status === 'uploading') {
        this.params.avatar = ''
        this.iconLoading = true
      }
      if (file.file.status === 'done') {
        this.params.avatar = file.file.response.data
        this.iconLoading = false
      }
    },
    sexChange(e) {
      this.params.gender = e.target.value
    },
    handleChangePhone() {
      this.isEditPhone = true
    },
    confirmPhone() {
    },
    cancelPhone() {
      this.isEditPhone = false
    },
    submit() {
      this.submitLoading = true
      this.updateManager({
        id: this.params.id,
        data: this.params
      }).then(res => {
        this.$message.succss('修改成功')
      }).finally(() => {
        this.submitLoading = false
      })
    },
    echoData() {
      this.params = Object.assign({}, this.params, this.user)
    }
  },
  created() {
    this.echoData()
  }
}
</script>
<style lang="scss" scoped>
.ant-form {
  .ant-form-item {
    width: 100%;
    margin-bottom: 20px;
  }
}
</style>
